import { Component, OnInit } from '@angular/core';
import {ProductType} from '../model/product-type';
import {ProductService} from '../services/product.service';

@Component({
  selector: 'app-manager-product',
  templateUrl: './manager-product.component.html',
  styleUrls: ['./manager-product.component.css']
})
export class ManagerProductComponent implements OnInit {
  productDetail = {};
  products: ProductType[];
  constructor(private productServices: ProductService) { }
  getPruducts() {
    this.productServices.getProducts().subscribe(data => this.products = data);
  }
  deleteProduct(id) {
    this.productServices.deleteProduct(id).subscribe(data => {
      data ? this.getPruducts() : alert('false delete Product');
    });
  }
  showProductDetail(id) {
    this.productServices.getProduct(id).subscribe(data => {
      this.productDetail = data;
    });
  }
  ngOnInit() {
    this.getPruducts();
  }

}
