import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {ProductType} from '../model/product-type';

@Injectable({
  providedIn: 'root'
})
export class ProductService {
  url = 'http://5cb89c151551570014da4181.mockapi.io/products';

  // productType: ProductType = new ProductType();
  constructor(private http: HttpClient) {
  }

  getProducts(): Observable<ProductType[]> {
    return this.http.get<ProductType[]>(this.url);
  }

  deleteProduct(id): Observable<ProductType[]> {
    return this.http.delete<ProductType[]>(`${this.url}/${id}`);
  }

  getProduct(id): Observable<ProductType[]> {
    return this.http.get<ProductType[]>(`${this.url}/${id}`);
  }

}
